import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CForm,
    CFormInput,
    CFormLabel,
    CFormFloating,
    CFormTextarea,
    CRow,
} from '@coreui/react'
import React, { useState, useEffect } from 'react'
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
  
const UpdateReport = () => {
    const [shift, setShift] = useState('');
    const [site, setSite] = useState('');
    const [pic, setPic] = useState('');
    const [jenisGiat, setJenisGiat] = useState('');
    const [giat, setGiat] = useState('');
    const [lokasi, setLokasi] = useState('');
    const [objek, setObjek] = useState('');
    const [jumlahKegiatan, setJumlahKegiatan] = useState('');
    const [temuan, setTemuan] = useState('');
    const [jumlahTemuan, setJumlahTemuan] = useState('');
    const [detailTemuan, setDetailTemuan] = useState('');
    const [tindakLanjut, setTindakLanjut] = useState('');
    const [kategoriTemuan, setKategoriTemuan] = useState('');
    const [keterangan, setKeterangan] = useState('');

    const { id } = useParams();
    const navigate = useNavigate();
    
    useEffect(() => {
        getReportById();
    }, []);
    
    const getReportById = async () => {
        const response = await axios.get(`http://localhost:5000/reports/${id}`);
        console.log(response.data);
        setShift(response.data.shift);
        setSite(response.data.site);
        setPic(response.data.pic);
        setJenisGiat(response.data.jenis_giat);
        setGiat(response.data.giat);
        setLokasi(response.data.lokasi);
        setObjek(response.data.objek);
        setJumlahKegiatan(response.data.jumlah_kegiatan);
        setTemuan(response.data.temuan);
        setJumlahTemuan(response.data.jumlah_temuan);
        setDetailTemuan(response.data.detail_temuan);
        setTindakLanjut(response.data.tindak_lanjut);
        setKategoriTemuan(response.data.kategori_temuan);
        setKeterangan(response.data.keterangan);
    };
    
    const updatedReport = async (e) => {
      e.preventDefault();
      try {
        await axios.patch(`http://localhost:5000/reports/${id}`, {
          shift,
          site,
          pic,
          jenisGiat,
          giat,
          lokasi,
          objek,
          jumlahKegiatan,
          temuan,
          jumlahTemuan,
          detailTemuan,
          tindakLanjut,
          kategoriTemuan,
          keterangan,
        });      
          navigate("/fitur/list-report");
      } catch (error) {
          console.log(error);
      }
    };
  
    return (
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
            <strong>Update</strong> <strong>Security</strong> <small>Report</small>
            </CCardHeader>
            <CCardBody>
              <p className="text-medium-emphasis medium">
                Edit data dibawah ini !
              </p>
              <CForm validated={true} onSubmit={updatedReport}>
                <CRow>
                  <CCol xs>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="shift" 
                        value={shift} 
                        onChange={(e) => setShift(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Shift"
                        required
                      />
                      <CFormLabel>Shift</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="site" 
                        value={site} 
                        onChange={(e) => setSite(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Site"
                        required
                      />
                      <CFormLabel>Site</CFormLabel>
                  </CFormFloating>
                  </CCol>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="pic" 
                        value={pic} 
                        onChange={(e) => setPic(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan PIC"
                        required
                      />
                      <CFormLabel>PIC</CFormLabel>
                    </CFormFloating>
                  </CCol>
                </CRow>
                <CRow>
                  <p>  </p>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="jenis_giat" 
                        value={jenisGiat} 
                        onChange={(e) => setJenisGiat(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Jenis Giat"
                        required
                      />
                      <CFormLabel>Jenis Giat</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="giat" 
                        placeholder="Giat" 
                        value={giat} 
                        onChange={(e) => setGiat(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Giat"
                        required
                      />
                      <CFormLabel>Giat</CFormLabel>
                  </CFormFloating>
                  </CCol>
                </CRow>
                <CRow>
                  <p>  </p>
                  <CCol xs>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="lokasi" 
                        placeholder="Lokasi" 
                        value={lokasi} 
                        onChange={(e) => setLokasi(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Lokasi"
                        required
                      />
                      <CFormLabel>Lokasi</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="objek" 
                        placeholder="Objek" 
                        value={objek} 
                        onChange={(e) => setObjek(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Objek"
                        required
                      />
                      <CFormLabel>Objek</CFormLabel>
                    </CFormFloating>
                  </CCol>
                </CRow>
                <CRow>
                  <p>  </p>
                  <CCol xs>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="jumlah_kegiatan" 
                        value={jumlahKegiatan} 
                        onChange={(e) => setJumlahKegiatan(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Jumlah Kegiatan"
                        required
                      />
                      <CFormLabel>Jumlah Kegiatan</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="temuan" 
                        value={temuan} 
                        onChange={(e) => setTemuan(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Temuan"
                        required
                      />
                      <CFormLabel>Temuan</CFormLabel>
                  </CFormFloating>
                  </CCol>
                  <CCol xs>
                  <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="jumlah_temuan" 
                        value={jumlahTemuan} 
                        onChange={(e) => setJumlahTemuan(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Jumlah Temuan"
                        required
                      />
                      <CFormLabel>Jumlah Temuan</CFormLabel>
                    </CFormFloating>
                  </CCol>
                </CRow>
                <CRow>
                  <p>  </p>
                  <CCol xs>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="detail_temuan" 
                        value={detailTemuan} 
                        onChange={(e) => setDetailTemuan(e.target.value)} 
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Detail Temuan"
                        required
                      />
                      <CFormLabel>Detail Temuan</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="tindak_lanjut" 
                        value={tindakLanjut} 
                        onChange={(e) => setTindakLanjut(e.target.value)} 
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Tindak Lanjut"
                        required
                      />
                      <CFormLabel>Tindak Lanjut</CFormLabel>
                    </CFormFloating>
                  </CCol>
                </CRow>
                <CRow>
                  <p>  </p>
                  <CCol xs={4}>
                    <CFormFloating>
                      <CFormInput 
                        type='text' 
                        id="kategori_temuan" 
                        value={kategoriTemuan} 
                        onChange={(e) => setKategoriTemuan(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Kategori Temuan"
                        required
                      />
                      <CFormLabel>Kategori Temuan</CFormLabel>
                    </CFormFloating>
                  </CCol>
                  <CCol xs={8}>
                    <CFormFloating>
                      <CFormTextarea 
                        type='text' 
                        id="keterangan" 
                        value={keterangan} 
                        onChange={(e) => setKeterangan(e.target.value)}
                        feedbackValid="Sesuai!"
                        feedbackInvalid="Masukkan Keterangan (jika tidak ada masukkan TIDAK ADA)"
                        required  
                      />
                      <CFormLabel>Keterangan</CFormLabel>
                    </CFormFloating>
                  </CCol>
                <p>  </p>
                <CCol xs>
                  <CButton 
                    type='submit'
                    color="success" 
                    shape='rounded-pill'
                  >
                    Update
                  </CButton>
                </CCol>
                </CRow>
              </CForm>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    )}
  
    export default UpdateReport;